body {
  background-image: url(/static/media/background.20dc1692.jpg);
    background-size: cover; 
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
} */
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.container {
    background-color: rgba(4, 110, 96, 0.301);;
    text-shadow: .5px .5px .5px hotpink;
    height: auto;
    width: 95%;
    margin: 5px;
    border-style: solid;
    border-color: hotpink;
    border-width: 1px;
    border-radius: 10px;
    box-shadow: 10px 2px 2px rgba(0, 0, 0, 0.527);
    padding: 10px;
}

button:hover{
    background-color:rgba(86, 207, 15, 0.76);
    box-shadow: 0 0.4rem 1.4rem 0 rgba(247, 6, 215, 0.5);
    border-style: ridge;
    border-width: 1px;
    border-color: black;
    transform: scale(1.09);
    transform: transform 150ms;
    cursor: pointer;
}
.footer {
    margin: 5px;
    min-height: 40px;
    position: relative;
    padding: 10px;  
}
  
.footer span {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 3px;
    text-align: center;
}
a{
    color:rgba(33, 207, 85, 0.671);
    text-shadow: 1px 1px 1px black;
}
.nav-btn{
    background-color: rgba(33, 207, 85, 0.671);
    color: black;
    font-weight: 400;
    text-align: center;
    border-radius:12px;
    width: 140px;
    max-width: 50%;
    box-shadow: 0 0.4rem 1.4rem 0 rgba(0, 0, 0, 0.527);
    margin: 5px;
}
